@import url(https://fonts.googleapis.com/css?family=Rubik+Mono+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.Header {
  min-height: 5rem;
  background-color: #003153;
  padding: 0rem 1rem;
  display: -webkit-flex;
  -webkit-align-items: center;
  display: flex;
  align-items: center; }
  .Header a {
    text-decoration: none;
    color: #efefef; }
  .Header input {
    width: 12rem;
    float: right;
    border-radius: 1rem;
    margin: .5rem 0rem 0rem 2rem; }
  .Header span {
    font-family: 'Rubik Mono One', sans-serif;
    color: #efefef;
    font-size: 2rem; }
    .Header span.links {
      font-size: 1rem; }
  @media (min-width: 768px) {
    .Header input {
      width: 16rem; } }
  @media (min-width: 992px) {
    .Header input {
      width: 16rem; } }
  @media (min-width: 1200px) {
    .Header input {
      width: 20rem; } }

.Footer {
  background-color: #efefef;
  height: 3rem; }
  .Footer .container {
    padding: 10px;
    text-align: center; }
    .Footer .container .spot-price {
      font-size: 1.2rem;
      color: #FFA500;
      font-weight: 900;
      padding-left: .3rem; }

.Home {
  padding: 1rem; }
  .Home .container {
    text-align: center; }
    .Home .container input {
      width: 22rem;
      font-size: 1rem;
      text-align: center; }
    @media (min-width: 768px) {
      .Home .container input {
        width: 22rem; } }
    @media (min-width: 992px) {
      .Home .container input {
        width: 22rem; } }
    @media (min-width: 1200px) {
      .Home .container input {
        width: 30rem;
        font-size: 1.4rem; } }

#AddressInfo table {
  margin: auto;
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: .8rem;
  border: solid 1px #efefef; }
  #AddressInfo table td {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap; }
  #AddressInfo table tr:nth-child(odd) {
    background-color: #efefef; }
  #AddressInfo table td:first-of-type {
    font-weight: 900;
    font-size: .8rem; }

@media (min-width: 768px) {
  #AddressInfo {
    font-size: 1rem; }
    #AddressInfo table td {
      display: table-cell; } }

.tx-info div {
  cursor: pointer; }
  .tx-info div .tx-price {
    float: right; }

.tx-info table {
  width: 100%;
  margin-top: .5rem;
  border-collapse: collapse; }
  .tx-info table tbody {
    font-weight: 400; }
    .tx-info table tbody:nth-child(even) {
      background-color: #e6e6e6; }
    .tx-info table tbody:nth-child(odd) {
      background-color: #ffffff; }
  .tx-info table.hidden {
    display: none; }

#TxList {
  text-align: center;
  font-size: .6rem;
  font-weight: bold; }
  #TxList .item {
    margin: auto;
    padding: .5rem;
    border: solid 1px;
    width: 350px; }
    #TxList .item:nth-child(odd) {
      background-color: #e6e6e6; }
  #TxList .load-more {
    margin: 1rem;
    width: 250px;
    font-size: 1.5rem;
    padding: .5rem 1rem;
    border: transparent;
    color: #ffffff;
    background-color: #003153;
    border-radius: 2px;
    cursor: pointer; }
    #TxList .load-more:hover {
      background-color: #001133; }
  @media (min-width: 768px) {
    #TxList {
      font-size: 1rem; }
      #TxList .item {
        width: 575px; } }

.Address {
  padding: 1rem; }

@-webkit-keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spinner {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }
  .Address canvas {
    display: block;
    margin: auto; }
  .Address .spinner-box .spinner:before {
    content: '';
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 125px;
    height: 125px;
    margin-top: -10px;
    margin-left: -10px;
    border-radius: 50%;
    border: 2px solid #ccc;
    border-top-color: #000;
    -webkit-animation: spinner .6s linear infinite;
            animation: spinner .6s linear infinite; }
  .Address h2 {
    font-size: 1rem;
    text-align: center; }
  .Address .error {
    text-align: center;
    color: #cc2222;
    font-weight: 900; }
  .Address .tx-list table {
    min-width: 65%; }
  @media (min-width: 768px) {
    .Address h2 {
      font-size: 1.5rem; }
    .Address .tx-item {
      width: 100%; } }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

#root {
  min-height: 100vh; }

main {
  flex: 1 1;
  min-height: calc(100vh - 10rem);
  padding: 2rem 0rem; }

.App .container {
  margin: auto; }

@media (min-width: 768px) {
  .App .container {
    width: 750px; } }

@media (min-width: 992px) {
  .App .container {
    width: 970px; } }

@media (min-width: 1200px) {
  .App .container {
    width: 1170px; } }

